<template>
  <div>
    <div class="bodyContent">
      <el-row class="pay-card" type="flex" align="middle">
        <el-col :span="8">
          <el-radio v-model="radio" label="1">
            <i style="margin: 0 10px" class="el-icon-help"></i>
            中国工商银行</el-radio
          >
        </el-col>
        <el-col :span="4" :offset="4">***</el-col>
        <el-col :span="4" :offset="2">卡|快捷</el-col>
        <el-col :span="8" :offset="2"
          >支付
          <span style="color: orange; font-size: 18px; font-weight: 600">{{
            totalPrice
          }}</span>
          元</el-col
        >
      </el-row>
      <div class="pay-button">
        <el-button type="">其他方式付款</el-button>
        <el-button type="">海外地区方式付款</el-button>
        <el-button type="">添加银行卡付款</el-button>
        <el-button type="">使用网银付款</el-button>
      </div>
      <div class="pay-check">
        <i class="el-icon-circle-check"></i>
        安全设置检测成功！
      </div>
      <div class="pay-password">
        支付宝支付密码：
        <el-input v-model="password" type="password"></el-input>
        <p>请输入6位数字支付密码</p>
      </div>
      <el-button @click="submit" class="btn-submit" type="primary"
        >确定</el-button
      >
      <img class="pay-img" src="@/assets/pay.png" />
    </div>
    <div class="footer"></div>
  </div>
</template>
<script>
import Icon, { types } from "@/components/Icon/index.vue";
import http from "@/utils/request.js";
export default {
  components: {
    Icon,
  },
  computed: {
    // totalPrice() {
    //   return this.cart
    //     .map((item) => item.price * item.count)
    //     .reduce((i1, i2) => {
    //       return i1 + i2;
    //     });
    // },
  },
  data() {
    return {
      password: "",
      radio: "1",
      cart: [],
      totalPrice: 0,
      publicPath: process.env.BASE_URL,
      products: [],
      types,
      token: localStorage.getItem("token"),
    };
  },
  mounted() {
    console.log(process.env.BASE_URL);
    this.totalPrice = this.$store.state.totalPrice;
    this.cart = this.$store.state.cart;
  },
  methods: {
    submit() {
      this.$message({
        message: "支付成功",
        type: "success",
      });
      this.cart = [];
      this.$store.commit("updateCart", this.cart);
      this.$router.push("/");
    },
    pay() {
      this.$router.push("/pay");
    },
    goCart() {
      this.$router.push("/cart");
    },
    deleteItem(item) {
      console.log("删除：", item);
      this.$confirm("确定要删除吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.cart.splice(
          this.cart.findIndex((i) => i.id == item.id),
          1
        );
      });
    },
    addCart(item) {
      const index = this.cart
        .map((item) => item.id)
        .findIndex((i) => i === item.id);
      console.log("index");
      if (index > -1) {
        this.cart[index]["count"]++;
      } else {
        this.cart.push(item);
      }
      this.$message({
        message: "加购成功",
        type: "success",
      });
    },
    login() {
      console.log("=== login ===");
      this.$router.push("/login");
    },
    logout() {
      console.log("=== logout ===");
      this.$confirm("确定要退出登录吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        http
          .post("/api/user/account/logout")
          .then(() => {
            localStorage.clear();
            this.$router.push("/login");
          })
          .catch((err) => {
            console.log("err:", err);
          });
      });
    },
    chat() {
      console.log("=== chat ===");
      this.$router.push("/chat");
    },
  },
};
</script>

